<template>
	<div class="comment-container">
		<div class="comment-header">
			<div class="comment-zhong">
				<div class="comment-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="comment-ti">评论</div>
				<div></div>
			</div>
		</div>
		<div class="comment-content">
			<div class="comment-biaoti">标题</div>
			<div class="comment-kuang">
				<div class="comment-zi">
					<textarea v-model="title"></textarea>
				</div>
			</div>
			<div class="comment-biaoti">问题描述</div>
			<div class="comment-kuang1">
				<div class="comment-wenzi">
					<textarea v-model="content"></textarea>
				</div>
				<div class="comment-tianjia">
					<img src="../assets/btn_shangchuan.png">
				</div>
			</div>
			<div class="comment-flex">
				<div class="comment-xuan"  @click="collect">
					<img src="../assets/btn_niming.png"  v-show="downIcon"/>
					<img src="../assets/btn_niming2.png" v-show="!downIcon"/>
				</div>
				<div class="comment-tiwen">匿名提问</div>
			</div>
			<div class="comment-tijiao" @click="crt_comment()">提交</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				data: {},
				content: '',
				downIcon: false,
				anonymity:'0',
				discussionid: '1',
				title: ''
			}
		},
		methods: {
			collect() {
				//根据你的downIcon判断此时的排序是升序还是降序
				//...排序方式
				this.downIcon = !this.downIcon
			},
			back() {
				window.history.back();
			},
			crt_comment() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					discussionid: that.discussionid,
					content: that.content,
					title: that.title,
					anonymity:that.anonymity,
				};
				that.$http.post('/discussion/crt_comment', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						//						that.special_list = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.comment-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.comment-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.comment-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.comment-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.comment-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.comment-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.comment-biaoti {
		font-size: 1.4rem;
		color: #000000;
		padding: 1.7rem 1.5rem;
	}
	
	.comment-kuang {
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 3rem;
		margin: 0 1.5rem;
	}
	
	.comment-zi textarea {
		width: 100%;
		height: 3rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	
	.comment-wenzi textarea {
		width: 100%;
		height: 22rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	
	.comment-kuang1 {
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 22rem;
		margin: 0 1.5rem;
	}
	
	.comment-tianjia img {
		width: auto;
		height: 6rem;
		position: relative;
		top: -7rem;
		left: 0.5rem;
	}
	
	.comment-flex {
		display: flex;
	}
	
	.comment-xuan img {
		width: auto;
		height: 1.5rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.comment-tiwen {
		font-size: 1.5rem;
		color: #000000;
		padding: 0.9rem 0 1.6rem 0.5rem;
	}
	
	.comment-tijiao {
		font-size: 1.8rem;
		border: 1px solid #8CADF5;
		background: #8CADF5;
		text-align: center;
		margin: 1rem 1.5rem;
		border-radius: 5px;
		padding: 0.5rem 0;
		color: #FFFFFF;
	}
</style>


